* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: rgb(71, 113, 116);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

div {
  background: rgb(37, 24, 51);
  padding: 40px;
  border-radius: 30px;
}

h1 {
  font-size: 100px;
  font-weight: bolder;
  color: #fff;
}
